import React from 'react';
import { Row, Col } from "react-bootstrap";
import img from "./img/slider.jpg";
import Gallery from "./Gallery";
import "./Carousel.css";

const Carousel = (props) => {

    return <>
        <div className="container my-4">
            <Row>
                <Col lg={5} className="col-5-carousel">
                    <div id="carouselExampleIndicators" className="carousel slide d-block" data-bs-ride="carousel">

                        <section style={{ display: props.showArrows }}>
                            <div className="carousel-indicators">
                                <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to={0} className="active" aria-current="true" aria-label="Slide 1" />
                                <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to={1} aria-label="Slide 2" />
                                <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to={2} aria-label="Slide 3" />
                            </div>
                        </section>


                        <div className="carousel-inner">
                            <div className="carousel-item active">
                                <img src={img} style={{ height: '23rem' }} className="d-block w-100" alt="..." />
                            </div>
                        </div>
                        <button style={{ display: props.showArrows }} className="carousel-control-prev" type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide="prev">
                            <span className="carousel-control-prev-icon" aria-hidden="true" />
                            <span className="visually-hidden">Previous</span>
                        </button>
                        <button style={{ display: props.showArrows }} className="carousel-control-next" type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide="next">
                            <span className="carousel-control-next-icon" aria-hidden="true" />
                            <span className="visually-hidden">Next</span>
                        </button>
                    </div>
                </Col>
                <Col className="p-0 nd2-col-gallery" lg={7}>
                    <Gallery />
                </Col>
            </Row>
        </div>
    </>
}

export default Carousel;