    const [ddes1, setDes1] = useState('Cisco, Cisco Meraki, Huawei, PaloAlto Networks, CheckPoint, Alcatel-lucent, SonicWALL, WatchGuard, Ubiquiti Networks, Ruckus Wireless, Extreme Networks, Arista Networks, Aruba Networks, AVAYA, Polycom, HPE, Dell, Fortinet, Sophos, Juniper, Honeywell, Linksys, MikroTik, Netgear, TP-Link, NVidia, Seagate, Tripplite, , WD, APC, Schneider, 3M, Kaspersky, Zyxel, Rackmount.IT, Barracuda, Yealink, Grandstream, Moxa, Brocade, EnGenius, ADVA, Digitus etc')
    const [ddes2, setDes2] = useState('inklusive routrar, switchar, servrar, lagring, telepresence och videokonferenser, videovervakning, IP-telefoner, brandvggar, trdlsa AP: er och styrenheter, EHWIC / HWIC / VWIC-kort, SFP: er, minne & blixt, hrddisk , Kablar och alla typer av system- eller ntverksrelaterade produkter.')
    const [ddes3, setDes3] = useState('Vi tillhandahller komplett inkp och inkp av IT-hrdvara och mjukvara till bra priser och prismatchning r garanterad fr alla mrken som listas i vr portflj och vra konsulter finns tillgngliga fr att ge dig rd om produktval, licensiering, kontrakt, frnyelser och projektkrav.')
    const [pics, setPics] = useState(pic1)
