import React from "react";
import Carousel from 'react-elastic-carousel';

export default function App() {
    const array = [{
        name:'Ian Neil', title:'Good Company', des:'Fantastiska! Mycket bra kundservice. Leveransen var snabb och priset var ocks rimligt. Tack', d:'1 maj 2021'}
        ,{name:'Simon Max',title:'Perfekt affr', des:'Vi kpte flera Cisco SFP-moduler. Var och en var originell och ny, inte OEM. De gav 70% rabatt p GPL vilket var bra. Frakten tog en vecka, men det r okej.', d:'30 apr 2021' }
    ,{name:'Stefan Bengtsson', title:'Verkligen hjlpte oss', des:'Inte bara de gjorde en frsta bedmning av mitt ntverk fr att se vad som krvs, men FirewallForce kommer ocks upp med en arkitektonisk design och implementering som verkligen hjlpte oss i fr- och efterfrsljningsarbete.', d:'22 okt. 2020'},
    {name:'Gunilla Kllnere', title:'Jag ser FirewallForce som en', des:'Jag ser FirewallForce som en frlngning av mitt team inte som leverantr. De levererade utrustningen i tid och vi fick det bsta priset och drmed sparade vi mycket. De konfigurerade vr brandvgg fre skeppet och det fungerade precis enligt arbetets omfattning.', d:'2 dec. 2020'},
    {name:'Pontus Fredrik', title:'De har allt du behver fr att uppfylla', des:'De har allt du behver fr att uppfylla dina affrs- och IT-krav. Efter en fantastisk frsta upplevelse med fretaget har de blivit mina plitliga IT-leverantrer. Stabilitet, anpassningsbar hgkvalitativ, kostnadseffektiv, du fr valuta fr pengarna.', d:'23 nov. 2020'},
    {name:'Goran Sporre', title:'Den vergripande erfarenheten har varit', des:'Den vergripande erfarenheten har varit mycket trevlig fr min organisation. Cisco-routern jag fick var anpassad med skerhetslicens fr att tillgodose kraven i vrt ntverk och den stder verksamheten. Tack, FirewallForce fr ett vl utfrt arbete.', d:'5 nov. 2020'},
{name:'Robin Nyberg', title:'Deras priser och rabatter r helt enkelt fantastiska', des:'ven om de r nya inom affrsomrdet men de vet hur man arbetar. Vi kpte ngra avancerade modeller till vrt datacenter tillsammans med programvarulicenser. Det som r bra r att deras priser och rabatter r helt enkelt fantastiskt. Vi jmfrde deras priser med andra leverantrer men de sticker ut frn andra.', d:'26 okt. 2020'}
];
    const breakPoints = [
        { width: 1, itemsToShow: 1 },
        { width: 550, itemsToShow: 2 },
        { width: 768, itemsToShow: 2 },
        { width: 1200, itemsToShow: 4 },
    ]
    return <>
        <Carousel breakPoints={breakPoints} pagination={false}>
            {
                array.map((item, index) => {
                    return <div key={index} className="mx-1 ">
                        <div className="card-body card-width mt-4">
                            <section className="d-flex justify-content-between card-text-trust mt-4">
                                <div className="">
                                    <i className="fa fa-star text-success"></i>
                                    <i className="fa fa-star text-success"></i>
                                    <i className="fa fa-star text-success"></i>
                                    <i className="fa fa-star text-success"></i>
                                    <i className="fa fa-star text-success"></i>
                                </div>
                                <span>{item.d}</span>
                            </section>
                            <h3 className="card-title heading-trust mt-3 " style={{color:'#575757', fontFamily:'Poppins'}}>{item.title}</h3>
                            <p className="card-text card-text-trust mt-2 " style={{color:'#575757', fontFamily:'Poppins'}}>{item.des}</p>
                            <p className="card-text card-text-trust mt-2 " style={{color:'#575757', fontFamily:'Poppins'}}><small >{item.name}</small></p>
                        </div>
                    </div>
                })
            }
        </Carousel>
    </>
}