import React from 'react';
import "./style.css";

const ModalMapItems = () => {
    return (
        <section className="shadow-sm p-3 bg-white mx-2 mb-3 d-flex justify-content-center img-product-modal">
            <img className="img-product" src="data:image/jpeg;base64," alt="" />
            <div className="overlay-container1 text-center">
                <span className="text-capitalize font-size-small" style={{ marginBottom: "0px" }}>
                    Cabeling Equipment <br />
                    (5762)<br />
                </span>
            </div>
        </section>
    )
}

export default ModalMapItems;